.flex_table.gruvbox {
  scrollbar-width: thin;
  --flex_table_scrollbar_bg : var(--bg2);
  --flex_table_thumb_bg : var(--bg1);
  --flex_table_thumb_bg_hover : var(--bg0);
  scrollbar-color: var(--flex_table_thumb_bg) var(--flex_table_scrollbar_bg);
}
.flex_table.gruvbox::-webkit-scrollbar-track {
  background: var(--flex_table_scrollbar_bg);
}
.flex_table.gruvbox::-webkit-scrollbar-thumb {
  background-color: var(--flex_table_thumb_bg) ;
}

.flex_table.gruvbox::-webkit-scrollbar-thumb:hover {
  background-color: var(--flex_table_thumb_bg_hover) ;
}

