.arc_bar_label_top{
font-size: 24px;
font-family: inherit;
margin-top: 0px;
margin-bottom: 0px;
font-weight: 500;
}

.arc_bar_label_bottom{
font-size: 14px;
font-family: inherit;
font-weight: 500;
margin-top: 0px;
margin-bottom: 0px;
}

.arc_bar_tick{
  font-size: 10px;
  position: absolute;
  bottom: 0px;
  margin: 0px;
}

.arc_bar_tick_left{
  right:calc(100% + 3px)
}

.arc_bar_tick_right{
  left:calc(100% + 3px)
}