.ping_jobs_icon_container{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-left: 12px;
  cursor: pointer;
}

.ping_jobs_icon_container.ti.active::after{
  width: 48px;
  height: 48px;
  position: relative;
  background-color: var(--red_dark);
}
.ping_jobs_icon_container.gruvbox.active{
  background-color: var(--bg1);
}